import React, { Component } from 'react';
import { TopBar, QuotationButton, Footer } from '../../Fixed';

import style from './style.module.scss';
import { Container } from 'reactstrap';

export default class PrivacyLayout extends Component {

    render() {

        return (
            <div className={[style.fullsize].join(' ')}>
                <TopBar />
                <h1>POLÍTICA DE PRIVACIDADE</h1>
                <QuotationButton />
                <div className={[style.rgpd].join(' ')}>
                    <Container>
                        <div className={[style.text].join(' ')}>
                            <h3>
                                <p>Prezado(a) Cliente,</p>

                                <p> Na BORJA REIS queremos que os seus dados pessoais se encontrem sempre em segurança. Porque os nossos clientes são a nossa prioridade é fundamental que a sua privacidade também esteja assegurada.</p>

                                <p>Os dados pessoais recolhidos são usados <u>exclusivamente</u> na prestação dos nossos serviços e no envio de campanhas de marketing.</p>

                                <p>Com a entrada em vigor do novo <b>REGULAMENTO GERAL DA PROTECÇÃO DE DADOS PESSOAIS</b> a 25 de Maio de 2018, garantimos o direito de acesso, alteração ou eliminação dos seus dados, mediante um simples pedido dirigido à BORJA REIS através do e-mail <a href='mailto:borjareis@megaloja.pt'><u>borjareis@megaloja.pt</u></a>, ou junto das nossa lojas solicitando a um colaborador.</p>

                                <p>Caso pretenda manter os seus dados não terá de fazer nada. Só assim poderá continuar a receber as nossas comunicações e a ter acesso às nossas campanhas, novidades e outros serviços.</p>
<br></br>
                                <p>Em nome da Borja Reis agradecemos a sua atenção e colaboração.</p>
                            </h3>
                        </div>

                    </Container>
                </div>
                <Footer />
            </div>
        );
    }
}