import React, { Component } from 'react';
import { Button, Form, FormGroup, Label, Input, Container, Col } from 'reactstrap';

import style from './style.module.scss';

export default class QuotationForm extends Component {

    render() {
        return (
            <Container fluid={true} className={[style.form_quote].join(' ')}>
                <Col sm="12" md={{ size: 8, offset: 2 }}>
                    <Form>
                        <FormGroup>
                            <Label for="name" size='lg'>Nome</Label>
                            <Input name="name" id="formName" placeholder="Nome completo" />
                        </FormGroup>
                        <FormGroup>
                            <Label for="email" size='lg'>E-mail</Label>
                            <Input type="email" name="email" id="formEmail" placeholder="email@dominio.pt" />
                        </FormGroup>
                        <FormGroup tag="fieldset">
                            <Label for="quotationSelect" size='lg'>Tipo de Orçamento</Label>
                                <FormGroup check>
                                    <Label check>
                                        <Input type="radio" name="quotationTypeRadio" />{' '}
                                        Cozinhas por medida
                                </Label>
                                </FormGroup>
                                <FormGroup check>
                                    <Label check>
                                        <Input type="radio" name="quotationTypeRadio" />{' '}
                                        Materiais de Construção
                                </Label>
                                </FormGroup>
                                <FormGroup check disabled>
                                    <Label check>
                                        <Input type="radio" name="quotationTypeRadio" disabled />{' '}
                                        Mobiliário e Decoração [ Desactivado ]
                                </Label>
                                </FormGroup>
                        </FormGroup>
                        <FormGroup>
                            <Label for="exampleText">Pedido</Label>
                            <Input type="textarea" name="text" id="quotationText" />
                        </FormGroup>
                        <FormGroup check>
                            <Label check>
                                <Input type="checkbox" />{' '}
                                Concordo partilhar estas informações com a empresa
                            </Label>
                        </FormGroup>
                        <Button className={'ml_submit'}>Submeter</Button>
                    </Form>
                </Col>
            </Container>
        );
    }
}
