import React, { Component } from 'react';

import footerLinks from '../footer/footer';
import { FooterLinksHttpEntity } from '../../../entities/Entities';

import style from './style.module.scss'

interface QuotationButtonProps {
}

interface QuotationButtonState {
}

let quotationObject: FooterLinksHttpEntity;

export default class QuotationButton extends Component<QuotationButtonProps, QuotationButtonState> {

    constructor(props: QuotationButtonProps) {
        super(props);

        this.renderQuotation();
    }

    renderQuotation() {

        footerLinks.forEach(element => {

            if (element.title === 'Orçamentos') {
                quotationObject = element;
            }
        });
    }

    render() {
        return (
            <div className={[style.quotation_button].join(' ')}>
                <a href={quotationObject.url} className={[style.btn].join(' ')}>
                    <span>{quotationObject.title.toUpperCase()}</span>
                </a>
            </div>
        );
    }
}