import FooterLinks from './footer';
import { Link } from 'react-router-dom';
import React, { Component } from 'react';
import { Nav, NavLink, Navbar } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { FooterLinksHttpEntity } from '../../../entities/Entities';

import style from './style.module.scss';

interface FooterProps {

}

interface FooterState {

}

export default class Footer extends Component<FooterProps, FooterState> {

    renderFooter(footer: FooterLinksHttpEntity[]) {

        return (
            <Nav>
                {footer.map((footerLinks: FooterLinksHttpEntity, index: number) => {

                    // if footerLinks.url is empty, it's an empty anchor,
                    // working like a span, diving every link
                    return (footerLinks.url === '') ?
                        (
                            <NavLink key={index}>
                                {footerLinks.title}
                            </NavLink>
                        )
                        :
                        // if footerLinks.url includes # in the link,
                        // it renders a normal navlink, without router
                        (footerLinks.url.includes('#')) ?
                            (
                                <NavLink key={index} href={footerLinks.url}>
                                    {footerLinks.title}
                                </NavLink>
                            )
                            :
                            (
                                <NavLink key={index} tag={Link} to={footerLinks.url}>
                                    {footerLinks.title}
                                </NavLink>
                            );
                })
                }
            </Nav>
        );
    }

    render() {
        return (
            <div className={'foot_container'}>
                <Navbar
                    className={[style.footer].join(' ')} expand="md">

                    {this.renderFooter(FooterLinks)}

                    <Nav className={[style.footer_copyright].join(' ')}>
                        <span>
                            Mega Loja Borja Reis © {(new Date().getFullYear())}
                        </span>
                        <a href={'https://www.youtube.com/'}>
                            <FontAwesomeIcon size={'2x'} icon={'rss-square'} />
                        </a>
                        <a href={'https://www.google.com/'}>
                            <FontAwesomeIcon size={'2x'} icon={'check-square'} />
                        </a>
                    </Nav>
                </Navbar>

            </div>
        );
    }
}